.dashboard {
    padding: 6rem 2rem 4rem;
    max-width: 1200px;
    margin: 0 auto;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

.addButton {
    padding: 0.8rem 1.5rem;
    background-color: var(--tesla-red);
    color: white;
    border-radius: 4px;
    font-weight: 600;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1.5rem;
}

.card {
    background-color: rgba(255, 255, 255, 0.05);
    padding: 1.5rem;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.card h3 {
    margin-bottom: 0.5rem;
}

.card p {
    color: var(--tesla-gray);
    font-size: 0.9rem;
    margin-bottom: 1rem;
}

.stats {
    display: flex;
    justify-content: space-between;
    font-size: 0.9rem;
    margin-bottom: 1rem;
    color: var(--tesla-light);
}

.editButton {
    width: 100%;
    padding: 0.5rem;
    background-color: transparent;
    border: 1px solid var(--tesla-gray);
    color: white;
    border-radius: 4px;
    transition: all 0.3s ease;
}

.editButton:hover {
    background-color: white;
    color: var(--tesla-dark);
}

.formContainer {
    background-color: rgba(255, 255, 255, 0.05);
    padding: 2rem;
    border-radius: 8px;
    max-width: 800px;
    margin: 0 auto;
}

.formGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.inputGroup {
    display: flex;
    flex-direction: column;
}

.inputGroup label {
    margin-bottom: 0.5rem;
    color: var(--tesla-gray);
}

.inputGroup input,
.inputGroup textarea {
    padding: 0.8rem;
    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid transparent;
    border-radius: 4px;
    color: white;
    font-family: inherit;
}

.inputGroup input:focus,
.inputGroup textarea:focus {
    border-color: var(--tesla-light);
    outline: none;
}

.formActions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
}

.cancelButton,
.saveButton {
    padding: 0.8rem 2rem;
    border-radius: 4px;
    font-weight: 600;
}

.cancelButton {
    background-color: transparent;
    border: 1px solid var(--tesla-gray);
    color: white;
}

.saveButton {
    background-color: var(--tesla-red);
    color: white;
}